const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {

    dialogsData: [
        {
            id: 1,
            name: 'Andrey',
            avatar: 'https://images.cdn.yle.fi/image/upload/f_auto,fl_progressive/q_88/w_2944,h_1655,c_crop,x_0,y_18/w_1200/v1448279046/17-394495652fbc780a68.jpg'
        },
        {
            id: 2,
            name: 'Aloha',
            avatar: 'data:image/jpeg;base64,'
        },
        {
            id: 3,
            name: 'Misha',
            avatar: 'https://thumbs.dreamstime.com/b/oboe-%D0%BD%D0%B0-%D0%B1%D0%B5-%D0%BE%D0%B9-%D0%BF%D1%80%D0%B5-%D0%BF%D0%BE%D1%81%D1%8B-%D0%BA%D0%B5-37634543.jpg'
        },
        {
            id: 4,
            name: 'Gleb',
            avatar: 'https://jam.ua/files/images/items/TB450M-(S)-main-jmichael.jpg'
        },
        {
            id: 5,
            name: 'Tima',
            avatar: 'https://www.muzpro.eu/10442-large_default/yamaha-ybs-62e-eb-baritone-saxophone.jpg'
        },
    ],
    messageData: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'Ho'},
        {id: 3, message: 'He'},
        {id: 4, message: 'Hy'}
    ],

}

const dialogs_Reducer = (state = initialState, action) => {

    switch (action.type) {

        case SEND_MESSAGE:
            let newMessageText = action.values
            let idNumber =state.messageData.length+1
            return {
                ...state,
                //Добавил уникальный id не как у Димыча
                messageData: [...state.messageData, {id:idNumber, message:newMessageText}]
            };
        default :
            return state;
    }
}
export const sendMessageActionCreator = (values) => ({type: SEND_MESSAGE, values})

export default dialogs_Reducer;