import React from "react";
import s from './Post.module.css';



function Post(props) {

    return <div className={s.myPost_wrapper}>
        <div className={s.myPost_avatar}>
            <img
                src="data:image/png;base64,"
                alt="avatar"/>
        </div>
           <div className={s.myPost_message}>
               {props.message}
           </div>
            <div className={s.myPost_like}>
                <span >Like {props.likesCount}</span>
            </div>
    </div>
}

export default Post